import styled from "styled-components";
import { Label, Input } from "reactstrap";

export const Card = styled.div`
  background: #ffffff;
  border-radius: 15px;
  padding: 30px;
`;

export const LabelInput = styled(Label)`
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #9b96ab;
`;

export const InputUser = styled(Input)`
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  border: none;
  border-bottom: 0.5px solid #9b96ab;
`;
